.sectionContact{max-width:2000px;background:linear-gradient(#e6e6e6, #fcfcfc);margin:auto;padding-top:60px;display:flex;flex-direction:column;align-items:center;border-top:#0e2b07 1px dotted;padding-bottom:50px}.sectionContact-screen{background-size:cover;padding-top:0;background-repeat:no-repeat;padding-top:20px;box-sizing:border-box;border-top:none;padding-bottom:0}.sectionContact-background{background-image:url("../assets/forêtC.webp")}.boxContact{width:70%;margin:auto;display:flex;flex-direction:column;align-items:center;border-radius:25px;background-color:rgba(152,193,152,.3490196078);padding-top:20px;padding-bottom:20px;font-size:large}@media(max-width: 750px){.boxContact{width:90%}}.boxContact-screen{background-color:rgba(45,68,41,.77);padding:25px;border-top-left-radius:100px;border-bottom-right-radius:100px;box-sizing:border-box;margin-bottom:150px;margin-top:50px}.boxContact__anchor:link{color:rgba(9,77,4,.831372549)}.boxContact__anchor:visited{color:rgba(9,77,4,.831372549)}.boxContact__anchor-screen:link{color:#fff}.boxContact__anchor-screen:visited{color:#fff}.boxContact__icon{color:rgba(9,77,4,.831372549);margin-right:10px}.boxContact__icon-screen{color:#fff}